@charset "UTF-8";
:root {
  --font-garamonddisplay: "garamond-premier-pro-display", serif;
  --font-franklin: "franklin-gothic-urw-cond", sans-serif;
}

:root {
  --black: rgb(0, 0, 0);
  --white: rgb(220, 220, 220);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-garamonddisplay);
}

.main {
  flex: 1;
}

.container {
  padding: 1rem 2rem 0 2rem;
}

@font-face {
  font-family: "Blocus";
  src: url("/fonts/blocuswebfont.woff") format("woff"), url("/fonts/blocuswebfont.ttf") format("truetype"), url("/fonts/blocuswebfont.eot") format("eot");
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
}
h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--white);
}

.header__menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
}
.header__title {
  font-size: 5rem;
  font-family: "Blocus";
}
@media (max-width: 39.9375em) {
  .header__title {
    font-size: 3rem;
  }
}
.header__link {
  font-size: 2rem;
}

.site {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-size: 1.35rem;
  line-height: 1.2;
  color: var(--white);
}
@media (max-width: 39.9375em) {
  .site {
    font-size: 1.15rem;
  }
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
  max-width: 55vw;
  margin: 0 auto;
}
.main__contact {
  text-align: center;
}
.main__info {
  text-align: justify;
  -moz-text-align-last: center;
  text-align-last: center;
}

video {
  display: block;
  margin: auto;
}

#myVideo {
  position: fixed;
  width: 100%;
}

.spotlight {
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: radial-gradient(circle, transparent 150px, rgba(0, 0, 0, 0.9) 200px);
}
@media (max-width: 63.9375em) {
  .spotlight {
    display: none;
  }
}

.marquees {
  display: flex;
  overflow: hidden;
  font-size: 2rem;
  padding: 1rem;
}
.marquees:hover {
  animation-play-state: paused;
}
.marquees div {
  display: flex;
  flex: 1 0 auto;
  animation: marquees 30s linear infinite;
  will-change: transform;
}

@keyframes marquees {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(-100%, 0);
  }
}
.flier {
  pointer-events: none;
}
.flier img {
  width: 400px;
}

.flier > * {
  /* Adjust animation duration to change the element’s speed */
  animation: fly 75s linear infinite;
  pointer-events: none !important;
  top: 0;
  left: 0;
  transform: translateX(-120%) translateY(-120%) rotateZ(0);
  position: fixed;
  animation-delay: 1s;
  z-index: 999;
}

@keyframes fly {
  98.001%, 0% {
    display: block;
    transform: translateX(-200%) translateY(100vh) rotateZ(0deg);
  }
  15% {
    transform: translateX(100vw) translateY(-100%) rotateZ(180deg);
  }
  15.001%, 18% {
    transform: translateX(100vw) translateY(-30%) rotateZ(0deg);
  }
  40% {
    transform: translateX(-200%) translateY(3vh) rotateZ(-180deg);
  }
  40.001%, 43% {
    transform: translateX(-200%) translateY(-100%) rotateZ(-180deg);
  }
  65% {
    transform: translateX(100vw) translateY(50vh) rotateZ(0deg);
  }
  65.001%, 68% {
    transform: translateX(20vw) translateY(-200%) rotateZ(180deg);
  }
  95% {
    transform: translateX(10vw) translateY(100vh) rotateZ(0deg);
  }
}
video {
  display: block;
  margin: auto;
}

#myVideo {
  position: fixed;
  width: 100%;
  z-index: -999999999999;
}

.video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 2;
  z-index: -99999;
}