@use '../util/' as *;

video {
    display: block;
    margin: auto;
  }
  
  #myVideo {
    position: fixed;
    width: 100%;
    z-index: -999999999999;
  }
  
  .video-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.4);
    z-index: 2;
    z-index: -99999;
  }